import React from "react"
import { useStaticQuery, graphql } from "gatsby"

import EmptyLine from "../../components/empty-line"
import mockupTitanNewsImage from "../../images/mockupTitanNews.png"
import titanNewsFontsImage from "../../images/titannews-fonts.png"
import Work from "../../components/work"

export default () => {
  const { titanNews } = useStaticQuery(graphql`
    query {
      titanNews: file(relativePath: { eq: "titanNews.png" }) {
        childImageSharp {
          fluid(maxWidth: 1200) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `)
  return (
    <Work
      title="TitaNews"
      subTitle="Newspaper Website"
      children={
        <React.Fragment>
          <div>
            Designing the homepage of an online newspaper is a big challenge for
            alignment and arrangement due to a huge amount of content.
          </div>
          <EmptyLine variant="Paragraph" />
          <div>
            The content is arranged clean and clear as much as possible for
            users to follow the breaking news on the top, as well as related
            content by categories below. There is also an Offer section for
            advertisement and Gallery for videos and photos.
          </div>
        </React.Fragment>
      }
      mockupImage={mockupTitanNewsImage}
      colors={["#9f0101", "#2b2727", "#ffffff"]}
      fontImage={titanNewsFontsImage}
      designFluidImages={[titanNews.childImageSharp.fluid]}
      hasSecondaryBackgroundColor
    />
  )
}
